import Bmob from "hydrogen-js-sdk";
import unicard from '@/components/uni-card/uni-card.vue'
import uninumberbox from '@/components/uni-number-box/uni-number-box.vue'
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
import uniIcon from '@/components/uni-icon/uni-icon.vue'

let uid;
let that;
let value;
export default {
	components: {
		unicard,
		faIcon,
		uninumberbox,
		uniNavBar,
		uniIcon
	},
	data() {
		return {
			products: [],
			user: uni.getStorageSync("user"),
			value:'',
			type:'',
		}
	},

	onLoad(options) {
		console.log(options)
		uni.removeStorageSync("is_option")
		uid = uni.getStorageSync("uid")
		that = this
		value = options.value
		that.value = options.value
		that.type = options.type
		
		if (value == 1) {
			uni.setNavigationBarTitle({
				title: "销售退货"
			})
		}
		
		if (options.id) {
			uni.showLoading({
				title: "加载中..."
			})
			const query = Bmob.Query('Goods');
			if (options.isCode == "false") {
				query.equalTo("objectId", "==", options.id);
			} else {
				query.equalTo("productCode", "==", options.id)
			}
			query.equalTo("userId", "==", uid);
			query.equalTo("status", "!=", -1);
			query.include("stocks");
			query.find().then(res => {
				uni.setStorageSync("keepScan", true);
				if(res.length == 0){
					uni.showToast({
						icon:"none",
						title:"没有此产品"
					})
					uni.hideLoading();
					return;
				}
				
				if(res[0].order == 0){
					query.equalTo("userId", "==", uid);
					query.equalTo("objectId", "==", res[0].objectId);
					query.find().then(res => {
						for (let item of res) {
							item.num = 1;
							item.total_money = 1 * item.retailPrice;
							item.really_total_money = 1 * item.retailPrice;
							item.modify_retailPrice = item.retailPrice;
							if (item.models) {
								let count = 0
								for (let model of item.models) {
									model.num = 0
									count += 1
								}
								item.num = count
								item.selectd_model = item.models
								item.selected_model = item.models
							}
						}
						that.products = that.products.concat(res)
						wx.hideLoading()
					})
				}else{
					for (let item of res) {
						item.num = 1;
						item.total_money = 1 * item.retailPrice;
						item.really_total_money = 1 * item.retailPrice;
						item.modify_retailPrice = item.retailPrice;
						if (item.models) {
							let count = 0
							for (let model of item.models) {
								model.num = 0
								count += 1
							}
							item.num = count
							item.selectd_model = item.models
							item.selected_model = item.models
						}
					}
					that.products = that.products.concat(res)
					wx.hideLoading()
				}
			})
		} else {
			this.products = uni.getStorageSync("products");
			for (let item of this.products) {
				if (item.models) {
					let count = 0
					for (let model of item.models) {
						model.num = 0
						count += 1
					}
					item.num = count
					item.selectd_model = item.models
					item.selected_model = item.models
				}
			}
			that.products = this.products
		}
	},

	methods: {
		//扫码
		scanGoods() {
			uni.scanCode({
				success(res) {
					uni.showLoading({
						title: "加载中..."
					})
					let result = res.result;
					let array = result.split("-");
		
					const query = Bmob.Query('Goods');
					if (array[1] == "false") {
						query.equalTo("objectId", "==", array[0]);
					} else {
						query.equalTo("productCode", "==", array[0])
					}
					query.equalTo("userId", "==", uid);
					query.include("stocks");
					query.find().then(res => {
						if(res.length == 0){
							uni.showToast({
								icon:"none",
								title:"没有此产品"
							})
							uni.hideLoading();
							return;
						}
						
						if(res[0].order == 0){
							query.equalTo("userId", "==", uid);
							query.equalTo("header", "==", res[0].objectId);
							query.include("stocks");
							query.find().then(res => {
								for (let item of res) {
									item.num = 1;
									item.total_money = 1 * item.retailPrice;
									item.really_total_money = 1 * item.retailPrice;
									item.modify_retailPrice = item.retailPrice;
									if (item.models) {
										let count = 0
										for (let model of item.models) {
											model.num = 0
											count += 1
										}
										item.num = count
										item.selectd_model = item.models
										item.selected_model = item.models
									}
								}
								that.products.concat(res)
								wx.hideLoading()
							})
						}else{
							for (let item of res) {
								item.num = 1;
								item.total_money = 1 * item.retailPrice;
								item.really_total_money = 1 * item.retailPrice;
								item.modify_retailPrice = item.retailPrice;
								if (item.models) {
									let count = 0
									for (let model of item.models) {
										model.num = 0
										count += 1
									}
									item.num = count
									item.selectd_model = item.models
									item.selected_model = item.models
								}
							}
							that.products.concat(res)
							wx.hideLoading()
						}
					})
				},
				fail(res) {
					uni.showToast({
						title: '未识别到条形码',
						icon: "none"
					})
				}
			})
		},

		//头部确定点击
		confrim_this() {
			let products = uni.getStorageSync('products')
			
			if(that.type == "returing"){
				if (value == 1) {
					uni.navigateTo({
						url: "/pages/common/good_return/return_detail/return_detail"
					})
				} 
			}else if(that.type == "newReturing"){
				if (value == 1) {
					uni.navigateTo({
						url: "/pages/common/good_return/buyReturn/buyReturn"
					})
				} else if (value == 2) {
					uni.navigateTo({
						url: "/pages/common/good_return/purchaseReturn/purchaseReturn"
					})
				}
			}
			

		},

		//多类型产品数量改变  步骤很重要
		handleModelNumChange($event, index, key, item) {
			item.num = Number($event)
			this.products[index].selected_model[key] = item
			let _sumNum = 0;
			for (let model of this.products[index].selected_model) {
				_sumNum += model.num
			}
			//console.log(this.products[index].selected_model)

			this.products[index].num = _sumNum
			this.products[index].total_money = _sumNum * Number(this.products[index].modify_retailPrice)
			this.products[index].really_total_money = _sumNum * Number(this.products[index].retailPrice)
			uni.setStorageSync("products", this.products)
		},

		//数量改变
		handleNumChange($event, index) {
			//console.log($event,index)
			this.products[index].num = Number($event)
			this.products[index].total_money = Number($event) * Number(this.products[index].modify_retailPrice)
			this.products[index].really_total_money = Number($event) * Number(this.products[index].retailPrice)
			uni.setStorageSync("products", this.products)
		},

		//删除点击
		handleDel(index) {
			//console.log(index)
			if (this.products.length == 1) {
				uni.showToast({
					title: "最少选择一个产品",
					icon: "none"
				})
			} else {
				this.products.splice(index, 1);
				uni.setStorageSync("products", this.products)
			}
		},

		//实际价格输入
		getrealprice($event, index) {
			this.products[index].modify_retailPrice = $event.target.value
			this.products[index].total_money = this.products[index].num * Number($event.target.value)
			uni.setStorageSync("products", this.products)
			//console.log($event,index)
		},

	}
}
